h3,
h2 {
  position: relative;
  display: inline-block;
  z-index: 2;
  padding-bottom: 0.4em;
}

.testimonial_author {
  position: relative;
  padding-bottom: 0.2em;
}

h2:not(.h3, .project-detail__h2)::after,
h3:not(.services-h3)::after,
.testimonial_author::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 3rem;
  height: 0.3rem;
  background: var(--color-accent-400);
}

.title-small {
  display: flex;
  align-items: center;
  font-weight: 400;
}

.title-small:not(.project-detail__url):before {
  content: "";
  width: 0.5em;
  height: 0.5em;
  border-radius: 50%;
  background: var(--color-accent-400);
  margin-right: 0.5em;
}
